import * as React from "react";
import { graphql } from "react-apollo";

import { Map, TileLayer } from 'react-leaflet';
import { Sidebar, Tab } from 'react-leaflet-sidetabs'
import { FiHome, FiChevronRight, FiSearch, FiSettings } from "react-icons/fi";

import Application from "../application/application.component";
import Icon from "../application/icon.component";

const { I18n } = require("react-i18nify");

interface DecidimMapProps {
  loading?: boolean;
}

interface DecidimMapState {
  collapsed?: boolean;
  selected: string;
}

/**
 * The core class of the Decidim Map engine.
 * It renders a collection of comments given a commentable id and type.
 * @global
 * @class
 * @augments Component
 */
export class DecidimMap extends React.Component<DecidimMapProps,DecidimMapState> {

  constructor(props: any) {
    super(props);
    this.state = {
      collapsed: true,
      selected: 'home',
    };
  }

  public onClose() {
    this.setState({ collapsed: true });
  }

  public onOpen(id: any) {
    this.setState({
      collapsed: false,
      selected: id,
    })
  }

  public render() {

    let mapClasses = "decidim-map";
    // if (loading) {
    //   mapClasses += " loading-map";
    // }

    return (
      <div className={mapClasses} id="decidim-map">
        <Sidebar
          id="decidim-map-sidebar"
          position="left"
          collapsed={this.state.collapsed}
          closeIcon={<Icon name="icon-close" iconExtraClassName="icon--small" />}
          selected={this.state.selected}
          onOpen={this.onOpen.bind(this)}
          onClose={this.onClose.bind(this)}
        >
          <Tab id="home" header="Home" icon={<Icon name="icon-home" iconExtraClassName="icon--small" />}>
            <p>No place like home!</p>
          </Tab>
          <Tab id="search" header="Search" icon={<FiSearch />}>
            <p>The noblest search is the search for excellence!</p>
          </Tab>
          <Tab id="settings" header="Settings" anchor="bottom" icon={<FiSettings />}>
            <p>We don't want privacy so much as privacy settings!</p>
          </Tab>
        </Sidebar>

        <Map className="mapStyle" center={[0, 0]} zoom={7}>
          <TileLayer
            attribution=""
            url={'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png'}
          />
        </Map>
      </div>
    );
  }

}

/**
 * Wrap the DecidimMap component with a GraphQL query and children
 * fragments.
 */

window.DecidimMap = DecidimMap;

export interface DecidimMapApplicationProps {
  locale: string;
}

/**
 * Wrap the DecidimMapWithData component within an Application component to
 * connect it with Apollo client and store.
 * @returns {ReactComponent} - A component wrapped within an Application component
 */
const DecidimMapApplication: React.SFC<DecidimMapApplicationProps> = ({ locale }) => (
  <Application locale={locale}>
    <DecidimMap />
  </Application>
);

export default DecidimMapApplication;
